async function startServer(compilation, opts) {
  const {
    analyzerPort = 8888,
    host = '127.0.0.1',
    // reportTitle = 'this is giao'
  } = opts || {};

 
console.log('abc');


// http   websocket 
  const server = http.createServer((req, res) => {
    // console.log('req',req);
    // console.log('res',res);
    res.writeHead(200, { 'Content-Type': 'text/html' ,
                        'Connection': 'Upgrade' ,
                        'Upgrade': 'websocket' ,})
    // console.log('compilation',compilation.assets);
    // const html=renderView(compilation,reportTitle)
    app.get('/', function (req, res) {
      res.end(__dirname + '/index.html');
    });
  })




  new Promise(resolve => {
    server.listen(analyzerPort, host, () => {
      const url = `http://${host}:${analyzerPort}`;
      console.log('le');
      
      open(url);
      resolve('')

    });
  }).then(()=>{

    console.log('test'); // websocket

    const wss = new WebSocket.Server(server)
    
    open(url);
    
    
    wss.on('connection', ws => {
      console.log('connection');
      
      ws.on('error', err => {
        // Ignore network errors like `ECONNRESET`, `EPIPE`, etc.
        console.log('err', err);
  
        if (err.errno) return;
  
      });

      ws.on('message', function incoming(message) {
        console.log('server: received: %s', message);
    });

      ws.send('world');

      return {
        ws: wss,
        http: server,
      };
    
    });

   
    
    
  })
  
}